.faq {
  background-color: $color-ghost-white;
}

.faq__container {
  padding-top: 120px;
  padding-bottom: 120px;

  @include vp-1365 {
    padding-top: 100px;
    padding-bottom: 80px;
  }

  @include vp-767 {
    padding-top: 70px;
    padding-bottom: 70px;
  }
}

.faq__text-content h2 {
  margin: 0;
  margin-bottom: 50px;
  color: $color-primary;
  font-family: $ff-alumni-sans;
  font-size: 54px;
  font-weight: 600;
  line-height: 54px;
  letter-spacing: 1.62px;
  word-wrap: break-word;

  @include vp-1365 {
    font-size: 42px;
    line-height: 42px;
    letter-spacing: 1.26px;
    text-align: center;
  }

  @include vp-767 {
    margin-bottom: 37px;
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0.96px;
  }
}
